.alert {
  color: #333;
  font-size: 12px; //12pt;
  border: 1px solid #666;
  padding: 6px 21px 5px 34px;
  background-repeat: no-repeat;
  background-position: 7px 7px;
  position: relative;

  //icons
  &:before {
    content: '';
    position: absolute;
    height: 20px;
    left: 4px;
    top: 4px;
    width: 22px;
    background-image: url(../../img/form.png);
    background-repeat: no-repeat;
    background-position: -67px -33px; //default icon: warning
  }

  strong {
    font-weight: bold;
  }

  a {
    font-weight: normal;
    color: #2168A7;
    cursor: pointer;
  }

  small {
    color: #666;
    font-size: 11px; //11pt;
    font-weight: normal;
  }

  //remove icon
  span.form-control-feedback {
    color: #bbb;
    top: 3px;
    right: 2px;
    cursor: pointer;

    &:hover, &.hover{
      color: #2168A7;
    }
  }
}

@media
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi) {
  .alert {
    &:before {
      background-image: url(../../img/form-hdpi.png);
      background-size: 96px auto;
    }
  }
}

.alert-block {
  &:before {
    top: 13px;
  }
}

.alert-success {
  background-color: #E9FCDE;
  border-color: #B8D8A5;
  &:before {
    background-position: -44px -33px;
  }
}
.alert-warning {
  background-color: #FFF1E1;
  border-color: #F8D8B3;
  &:before {
    background-position: -67px -33px;
  }
}
.alert-danger {
  background-color: #FCECED;
  border-color: #EDBFBF;
  &:before {
    background-position: 0px -33px;
  }
}
.alert-info {
  background-color: #E7F3FE;
  border-color: #BED5E8;
  &:before {
    background-position: -22px -33px;
  }
}
